/* eslint eqeqeq: "off", camelcase: "off" */
require('./popups.scss')

// 下载线上任务助手弹层
const popup_download_assistant = {
  options: {
    effect: 'scale',
    cssClass: 'popup-download-assistant',
    buttons: [
      {text: '下载助手', theme: 'assertive'}
    ],
    showClose: true
  },

  template: (app_icon, app_name) => {
    return `
      <img class="app-icon" src="${app_icon}" alt="">
      <p class="app-name">${app_name}</p>
      <p class="tips">
        这是您的任务助手，每个人的助手可能不同，做任务期间必须开启哦
      </p>
    `
  }
}

// 提示安装微信，QQ弹层
const popup_install_app = {
  options: {
    effect: 'scale',
    cssClass: 'popup-general',
    buttons: [
      {text: '忽略'},
      {text: '下载'}
    ]
  },

  template: (appName) => {
    return `
      <div class="desc">您尚未安装“${appName}”</div>
    `
  }
}

// 收徒 - 二维码收徒弹层
const popup_apprentice_qrcode = {
  options: {
    effect: 'scale',
    cssClass: 'popup-apprentice-qrcode',
    showClose: true
  },
  template: (uid, qrcodeImg) => {
    return `
      <div class="background-transparent"></div>
      <div class="qrcode-content-wrapper">
        <canvas width="550" height="568" class="qrcode-content" id="qrcode-content" data-uid="${uid}" data-bg="${qrcodeImg}"></canvas>
      </div>
      <div class="qrcode-copy-content" id="qrcode-copy-content"></div>
      <div class="background-transparent desc">长按二维码可保存至相册</div>
    `
  }
}

// 收徒 - 成绩单收徒弹层
const popup_apprentice_card = {
  options: {
    effect: 'scale',
    cssClass: 'popup-apprentice-card',
    showClose: true
  },
  template: () => {
    return `
      <div class="background-transparent"></div>
      <div class="card-content-wrapper">
        <canvas id="card-content" class="card-content" width="550" height="726"></canvas>
      </div>
      <div class="card-copy-content" id="card-copy-content"></div>
      <div class="background-transparent desc">长按二维码可保存至相册</div>
    `
  }
}

// 收徒 - 链接分享弹层
const popup_apprentice_link = {
  options: {
    effect: 'scale',
    cssClass: 'popup-apprentice-link',
    showClose: true
  },
  template: (shoutuURl) => {
    let white_png = 'data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAEAAAABCAYAAAAfFcSJAAAAGXRFWHRTb2Z0d2FyZQBBZG9iZSBJbWFnZVJlYWR5ccllPAAAABBJREFUeNpi/v//PwNAgAEACQsDAUdpTjcAAAAASUVORK5CYII='

    return `
      <div class="background-transparent"></div>
      <div class="link-content">
        <div class="link-copy-area">
          <img class="copy-img" src="${white_png}" alt="${shoutuURl}">
          <div class="copy-select"></div>
          <div class="copy-text"><span>${shoutuURl}</span></div>
        </div>
        <div class="info">他人点击该链接加入钱咖即成为你的徒弟</div>
      </div>
      <div class="background-transparent desc">长按链接可拷贝</div>
    `
  }
}

// 用户成绩单
const popup_apprentice_common = {
  options: {
    effect: 'scale',
    cssClass: 'popup_apprentice_common',
    buttons: [],
    showClose: true
  },
  template: (args, hideReward = false) => {
    let reward = ''
    let granson_part = ''
    // if (!hideReward) {
    //   reward = `<div class="reward">为我带来奖励 ${args.my_apprentice_income} 元 </div>`
    // }

    if (args.shoutu_type == 2) { // new user
      granson_part = `<div class="hairline"></div><div class="content"><span class="left">徒孙人数</span><span class="right">${args.grandson_num} 人</span></div>`
    }

    return `
      <div class="moon"><img src="//qianka.b0.upaiyun.com/images/5372f4c319a0483db39a8dbbc3cefdb7.png" alt=""></div>
      <div class="user"><img src="${args.avatar}" alt="" class="avatar"><span>${args.nickname}</span> ${reward}</div>
      <div class="income"><div class="content"><span class="left">累计收入</span><span class="right">${args.total_income} 元</span></div><div class="hairline"></div><div class="content"><span class="left">收徒收入</span><span class="right">${args.apprentice_income} 元</span></div></div>
      <div class="apprentice"><div class="content"><span class="left">徒弟人数</span><span class="right">${args.apprentice_num} 人</span></div> ${granson_part} </div>
    `
  }
}

// lite 版钥匙 分享前置弹层
const popup_before_share = {
  options: {
    effect: 'scale',
    cssClass: 'popup-download-assistant',
    buttons: [
      {text: '下载安装', theme: 'light'},
      {text: '开启助手并分享', theme: 'light'}
    ],
    showClose: true
  },

  template: (app_icon, app_name) => {
    return `
      <img class="app-icon" src="${app_icon}" alt="">
      <p class="app-name">${app_name}</p>
      <p class="tips">
        即将通过任务助手进行分享<br>请确保助手开启哦
      </p>
    `
  }
}

const popup_rule_new = {
  options: {
    effect: 'scale',
    cssClass: 'popup-general',
    buttons: [
      {text: '知道了'}
    ]
  },

  template: `
    <div class="title">奖励说明</div>
    <div class="desc">
    徒弟、徒孙完成以下类型任务我可获奖励：标准任务、简易任务、专属任务、注册赚钱、办卡赚钱
    </div>
  `
}

const popup_rule_old = {
  options: {
    effect: 'scale',
    cssClass: 'popup-general',
    buttons: [
      {text: '知道了'}
    ]
  },

  template: `
    <div class="title">奖励说明</div>
    <div class="desc">
    徒弟完成以下类型任务，我可获奖励：标准任务、专属任务
    </div>
  `
}

module.exports = {
  popup_download_assistant,
  popup_install_app,
  popup_apprentice_qrcode,
  popup_apprentice_card,
  popup_apprentice_link,
  popup_apprentice_common,
  popup_before_share, // lite
  popup_rule_new,
  popup_rule_old
}



