<!-- 我的收徒：徒弟/徒孙列表 -->
<template>
  <div class="apprentice-list-component">
    <img v-if="showContent && apprentices.length == 0 && type == 1" width="375" height="146"
      style="position: absolute; left: 0px; top: 130px;"
      src="//qianka.b0.upaiyun.com/images/a158497e41e09033ada86ae3dceaa388.png">

    <img v-if="showContent && apprentices.length == 0 && type == 2" width="375" height="146"
      style="position: absolute; left: 0px; top: 130px;"
      src="//qianka.b0.upaiyun.com/images/00ebd2a94d052b5384795a2b79f31f0c.png">

    <scroll v-if="showContent && apprentices.length > 0" class="container" :on-infinite="infinite">
      <div style="height: 10px;"></div>
      <list class="list-ios apprentice-list">
        <item class="apprentice hl-item-borderless-top" v-for="a in apprentices" :key="a.user_id" @click.native="showApprenticePopup(a.user_id)">
          <div class="avatar">
            <img :src="a.avatar" alt="">
          </div>
          <span class="nickname" v-text="a.nickname"></span>

          <div class="income assertive">
            <span class="yuan">￥</span>{{ parseFloat(a.income) }}
          </div>

          <scalable height="14" class="time">
            <span v-text="a.timestamp"></span>
          </scalable>
        </item>
      </list>
      <div v-if="noMoreData" slot="infinite" class="text-center" v-text="noMoreDataTips"></div>
    </scroll>
  </div>
</template>

<style lang="scss">
@import '../../../scss/mixins';

.apprentice-list-component {
  width: 100%;
  height: 100%;
  /*max-height: 463px;*/
  position: relative;

  .label-loading {
    display: none;
  }

  .infinite-preloader {
    margin-top: 20px;
    margin-bottom: 10px;
  }
}

.tips-no-prentice {
  position: absolute;
  left: 0;
  top: 198px;
  width: 100%;
  text-align: center;
  color: #C5C4C3;
  font-size: 16px;
  line-height: 22px;
}

.container {
  top: 0px;

  .apprentice-list {
    margin-bottom: 0px;

    .apprentice {
      height: 76px;
      padding: 16px 0;
      position: relative;

      .avatar {
        padding: 2px;
        border-radius: 6px;
        position: absolute;
        left: 0;
        top: 16px;

        img {
          width: 38px;
          height: 38px;
          display: block;
          border-radius: 4px;
        }
      }

      .avatar:before {
        content: "";
        position: absolute;
        top: -50%;
        bottom: -50%;
        left: -50%;
        right: -50%;
        -webkit-transform: scale(0.5);
        transform: scale(0.5);
        border-style: solid;
        border-width: 1px;
        border-color: #DDDDDD;
        -webkit-border-radius: 6px;
        border-radius: 6px;
      }

      .time {
        line-height: 18px;
        position: absolute;
        bottom: 16px;
        left: 44px + 14px;
        color: #b5b5b5;
        font-size: 26px;
      }

      .nickname {
        position: absolute;
        top: 15px;
        left: 44px + 12px;
        font-size: 16px;
        line-height: 22px;

        @include font-regular();
      }

      .income {
        position: absolute;
        min-width: 53px;
        height: 22px;
        top: 27px;
        right: 16px;
        text-align: right;
        font-size: 16px;
        line-height: 22px;
        padding-right: 10px;
        color: #3A3A3A;

        @include font-medium();

        .yuan {
          font-size: 11px;
        }
      }
    }
  }
}

</style>

<script>
  import { popup_apprentice_common } from '../popups.js'

  export default {
    props: {
      apprentices: Array,
      showContent: Boolean,
      offset: Number,
      noMoreData: Boolean,
      noMoreDataTips: String,
      refresh: Function,
      infinite: Function,
      type: String
    },

    methods: {
      resetParams () {
        this.showContent = false
        this.apprentices = []
        this.offset = undefined
      },

      showApprenticePopup (userId) {
        $api
          .shoutuUserInfo(userId)
          .then(res => res.data && res.data.payload)
          .then(payload => {
            _.extend(payload, {
              my_apprentice_income: parseFloat(payload.my_apprentice_income),
              total_income: parseFloat(payload.total_income),
              nickname: payload.nickname || payload.user_id,
              apprentice_income: parseFloat(payload.apprentice_income)
            })

            let popup = $popup.fromTemplate(
              popup_apprentice_common.template(payload),
              popup_apprentice_common.options
            )
            popup.show()
          })
      }
    }
  }
</script>



